const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property({type: cc.SceneAsset, displayName: '跳转目标场景'})
    scene: cc.SceneAsset = null;

    loadScene() {
        if (this.scene != null) {
            cc.director.loadScene(this.scene.name);
        }
    }
}
